<script setup>
import { onMounted, defineProps, defineEmits } from 'vue';
const props = defineProps([
    "selectedConcepts", 
    "selectedEdges", 
    "selectedModule"
])

const menuEmit = defineEmits([
    'boxSelect', 
    'emitHandler'
])

let isOpen = true

function toggleOpen() {
    const menuButtons = document.querySelectorAll(isOpen ? ".collapse-in" : ".collapse-out")
    isOpen = !isOpen

    if (isOpen) {
        for (let animation of menuButtons) {
            animation.beginElement()
        }
        resizeSvg()

    } else {
        resizeSvg()
        for (let animation of menuButtons) {
            animation.beginElement()
        }
    }
}

onMounted(() => toggleOpen())

function menuClick(event) {
    console.log(event)
    if (event !== "boxSelect") {
        menuEmit("emitHandler", event)
    } else {
        menuEmit("boxSelect")
    }
}

function panelOnClick() {
    toggleOpen()
}

function resizeSvg() {
    const svg = document.querySelectorAll(!isOpen ? ".shrink" : ".grow");
    const buttonGroup = document.getElementById("g839");

    const btnScale = isOpen ? 1 : 2
    const transform = `matrix(${btnScale},0,0,${btnScale},1.0176234,1.124179)`
    buttonGroup.setAttribute('transform', transform);

    for (let animation of svg) {
        animation.beginElement()
    }

}
</script>

<style scoped>
#g839 {
    transform-origin: center;
    transform-box: fill-box;
    transition: .25s;
}

.menu-btn:hover {
    transform-origin: center;
    transform: scale(.85);
    transform-box: fill-box;
}

.menu-btn:active>path {
    fill: #00629b !important;
    box-shadow: 0px 0px 5px 1px black;
}

#join-concepts {
    fill: v-bind('props.selectedConcepts.length > 1 ? "#0f8800" : "#4c4c4c"');
}
#module-add {
    fill: v-bind('props.selectedConcepts.length > 0 && props.selectedModule ? "#0f8800" : "#4c4c4c"');
}
#delete-concept{
    fill: v-bind('props.selectedConcepts.length > 0 ? "#970000" : "#4c4c4c"');
}

#moduleDeleteBtn {
    fill: v-bind('props.selectedConcepts.length > 0 && props.selectedModule ? "#970000" : "#4c4c4c"');
}

#delete-junction-btn {
    fill: v-bind('props.selectedEdges.length > 0 ? "#970000" : "#4c4c4c"');
}
</style>
